@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);
header {
  height: 100vh;
  padding-top: 7rem;
  overflow: hidden;
}
.header__container {
  text-align: center;
  height: 100%;
  position: relative;
}
/* -------------------Hello I'm-------------------- */
.hello-bg {
  color: var(--color-bg);
  -webkit-animation: wiggle 2s linear infinite;
          animation: wiggle 2s linear infinite;
  background-color: var(--color-primary);
  margin: 0 auto;
  width: 75px;
  height: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  position: relative;
}
@-webkit-keyframes wiggle {
  0%, 7% {
    -webkit-transform: rotateZ(0);
            transform: rotateZ(0);
    -webkit-transform-origin: bottom;
            transform-origin: bottom;
  }
  15% {
    -webkit-transform: rotateZ(-15deg);
            transform: rotateZ(-15deg);
    -webkit-transform-origin: bottom;
            transform-origin: bottom;
  }
  20% {
    -webkit-transform: rotateZ(10deg);
            transform: rotateZ(10deg);
    -webkit-transform-origin: bottom;
            transform-origin: bottom;
  }
  25% {
    -webkit-transform: rotateZ(-10deg);
            transform: rotateZ(-10deg);
    -webkit-transform-origin: bottom;
            transform-origin: bottom;
  }
  30% {
    -webkit-transform: rotateZ(6deg);
            transform: rotateZ(6deg);
    -webkit-transform-origin: bottom;
            transform-origin: bottom;
  }
  35% {
    -webkit-transform: rotateZ(-4deg);
            transform: rotateZ(-4deg);
    -webkit-transform-origin: bottom;
            transform-origin: bottom;
  }
  40%, 100% {
    -webkit-transform: rotateZ(0);
            transform: rotateZ(0);
    -webkit-transform-origin: bottom;
            transform-origin: bottom;
  }
}
@keyframes wiggle {
  0%, 7% {
    -webkit-transform: rotateZ(0);
            transform: rotateZ(0);
    -webkit-transform-origin: bottom;
            transform-origin: bottom;
  }
  15% {
    -webkit-transform: rotateZ(-15deg);
            transform: rotateZ(-15deg);
    -webkit-transform-origin: bottom;
            transform-origin: bottom;
  }
  20% {
    -webkit-transform: rotateZ(10deg);
            transform: rotateZ(10deg);
    -webkit-transform-origin: bottom;
            transform-origin: bottom;
  }
  25% {
    -webkit-transform: rotateZ(-10deg);
            transform: rotateZ(-10deg);
    -webkit-transform-origin: bottom;
            transform-origin: bottom;
  }
  30% {
    -webkit-transform: rotateZ(6deg);
            transform: rotateZ(6deg);
    -webkit-transform-origin: bottom;
            transform-origin: bottom;
  }
  35% {
    -webkit-transform: rotateZ(-4deg);
            transform: rotateZ(-4deg);
    -webkit-transform-origin: bottom;
            transform-origin: bottom;
  }
  40%, 100% {
    -webkit-transform: rotateZ(0);
            transform: rotateZ(0);
    -webkit-transform-origin: bottom;
            transform-origin: bottom;
  }
}
.hello-arrow {
  width: 0;
  height: 0;
  border-left: 15px solid transparent;
  border-right: 15px solid transparent;
  border-top: 15px solid var(--color-primary);
  position: absolute;
  top: 30px;
}
/* -------------------cta-------------------- */
.cta {
  margin-top: 2.5rem;
  display: flex;
  grid-gap: 1.2rem;
  gap: 1.2rem;
  justify-content: center;
}
/* -----------------socials------------------ */
.header__socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 0.8rem;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem;
}
.header__socials::after {
  content: "";
  width: 1px;
  height: 2rem;
  background: var(--color-primary);
}
/* --------------me-------------- */
.me {
  background: linear-gradient(var(--color-primary), transparent);
  width: 22rem;
  height: 30rem;
  position: absolute;
  left: calc(50% - 11rem);
  margin-top: 4rem;
  border-radius: 12rem 12rem 0 0;
  overflow: hidden;
  padding: 5rem 1.5rem 1.5rem 1.5rem;
}
/* ---------------scroll down--------------- */
.scroll__down {
  position: absolute;
  right: -2.3rem;
  bottom: 5rem;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  font-weight: 300;
  font-size: 0.9rem;
}
/* ---------------medium------------------- */
@media screen and (max-width: 1024px) {
  header {
    height: 68vh;
  }
}

/* ---------------small------------------- */
@media screen and (max-width: 600px) {
  header {
    height: 100vh;
  }
  .header__socials,
  .scroll__down {
    display: none;
  }
}

nav {
  background: rgba(0, 0, 0, 0.3);
  width: -webkit-max-content;
  width: max-content;
  display: block;
  padding: 0.7rem 1.7rem;
  z-index: 2;
  position: fixed;
  left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  bottom: 2rem;
  display: flex;
  border-radius: 3rem;
  -webkit-backdrop-filter: blur(15px);
          backdrop-filter: blur(15px);
}
nav a {
  background: transparent;
  padding: 0.9rem;
  border-radius: 50%;
  display: flex;
  color: var(--color-light);
  font-size: 1.1rem;
}
nav a :hover {
  background: rgba(0, 0, 0, 0.3);
}
nav a.active {
  background: var(--color-primary);
  color: var(--color-bg);
}

.experience__container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 2rem;
  gap: 2rem;
}
.experience__container > div {
  background: var(--color-bg-variant);
  padding: 2.4rem 5rem;
  border-radius: 2rem;
  border: 1px solid transparent;
  transition: var(--transition);
}
.experience__container > div:hover {
  background: transparent;
  border-color: var(--color-primary-variant);
  cursor: default;
}
.experience__container > div h3 {
  text-align: center;
  margin-bottom: 2rem;
  color: var(--color-primary);
}
.experience__content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-row-gap: 2rem;
  row-gap: 2rem;
}
.experience__details {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
}
.experience__details-icon {
  margin-top: 6px;
  color: var(--color-primary);
}
/* ---------------medium------------------- */
@media screen and (max-width: 1024px) {
    .experience__container{
        grid-template-columns: 1fr;
    }
    .experience__container > div{
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }
    .experience__content{
        padding: 1rem;
    }
}

/* ---------------small------------------- */
@media screen and (max-width: 600px) {
    .experience__container{
        grid-gap: 1rem;
        gap: 1rem;
    }
    .experience__container > div{
        width: 100%;
        padding: 2rem 1rem;
    }
}

.about__container {
  display: grid;
  grid-template-columns: 35% 50%;
  grid-gap: 15%;
  gap: 15%;
}
.about__me {
  width: 100%;
  aspect-ratio: 1/1;
  border-radius: 2rem;
  background: linear-gradient(
    45deg,
    transparent,
    var(--color-primary),
    transparent
  );
  display: grid;
  place-items: center;
}
.about__me-image{
    border-radius: 2rem;
    overflow: hidden;
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
    transition: var(--transition);
}
.about__me-image:hover{
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
.about__cards{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 1.5rem;
    gap: 1.5rem;
    color: var(--color-white);
}
.about__card{
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    -webkit-transform: var(--transition);
            transform: var(--transition);
}
.about__card:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}
.about__icon{
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}
.about__card h5{
    font-size: 0.9rem;
}
.about__card small{
    font-size: 0.7rem;
    color: var(--color-light);
}
.about__content p {
    margin: 2rem 0 2.6rem;
    color: var(--color-pri);
}
/* ---------------medium------------------- */
@media screen and (max-width: 1024px) {
    .about__container{
        grid-template-columns: 1fr;
        grid-gap: 0;
        gap: 0;
    }
    .about__me{
        width: 50%;
        margin: 2rem auto 4rem;
    }
    .about__content p {
        margin: 1rem 0 1.5rem;
    }
  }
  
  /* ---------------small------------------- */
  @media screen and (max-width: 600px) {
    .about__me{
        width: 65%;
        margin: 0 auto 3rem;
    }
    .about__cards{
        grid-template-columns: 1fr 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }
    .about__content{
        text-align: center;
    }
    .about__content p {
        margin: 1.5rem 0;
    }
  }
  
.container.contact__container{
    display: grid;
    width: 58%;
    grid-gap: 12%;
    gap: 12%;
    grid-template-columns: 30% 58%;
}
.contact__option {
  background: var(--color-bg-variant);
  padding: 1.2rem;
  border-radius: 1.2rem;
  border: 1px solid transparent;
  text-align: center;
  transition: var(--transition);
}
.contact__option:hover {
    
  border-color: var(--color-primary-variant);
  background: transparent;
}
.contact__options{
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem
}
.contact__option-icon{
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}
.contact__option a{
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 0.8rem;
}

/* ----------form------------ */
form{
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}
input, textarea{
    width: 100%;
    padding: 1.5rem;
    border-radius: .5rem;
    background: transparent;
    border: 2px solid var(--color-primary-variant);
    resize: none;
    color: var(--color-white);
}
button{
    width: 130px!important;
}
/* ----------medium------------ */
@media screen and ( max-width:1024px) {
    .container.contact__container{
        grid-template-columns: 1fr;
        grid-gap: 2rem;
        gap: 2rem;
    }
}
/* ----------small------------ */
@media screen and (max-width:600px) {
    .container.contact__container{
        width: var(--container-width-sm);
    }
}
.services__container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 3rem;
  gap: 3rem;
}
.service {
  background: var(--color-bg-variant);
  border-radius: 0 0 2rem 2rem;
  border: 1px solid var(--color-primary);
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  transition: var(--transition);
}
.service:hover {
  background: transparent;
  border-color: var(--color-primary-variant);
  cursor: default;
}
.service__head {
  background: var(--color-primary);
  padding: 2rem;
  border-radius: 0 0 2rem 2rem;
  box-shadow: 0 2rem 1rem rgba(0, 0, 0, 0.1);
}
.service__head h3{
    color: var(--color-bg);
    font-size: 1rem;
    text-align: center;
}
.service__list{
    padding: 2rem;
}
.service__list li{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 0.8rem;
}
.service__list-icon{
    color: var(--color-primary);
    margin-top: 2px;
    font-size: 1.5rem;
}
.service__list p {
    font-size: .9rem;
}
/* --------------------medium----------------------- */
@media screen and (max-width: 1024px){
    .services__container{
        grid-template-columns: 1fr 1fr;
        grid-gap: 2rem;
        gap: 2rem;
    }
    .service{
        height: auto;
    }
}
/* --------------------small----------------------- */
@media screen and ( max-width: 600px) {
    .services__container{
        grid-template-columns: 1fr;
        grid-gap: 1.5rem;
        gap: 1.5rem;
    }
}
.portfolio__container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 2.5rem;
