* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

/* 
#F2B705
*/
:root {
  --color-bg: rgb(20, 20, 20);
  --color-bg-variant: #8C5C03;
  --color-primary: #ffbf00;
  --color-primary-variant: #BF7E04;
  --color-primary-variant-2:#8C5C03;
  --color-primary-variant-3:#593202;
  --color-white: #fff;
  --color-light: rgba(255, 255, 255, 0.6);

  --transition:all 400ms ease;

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;
}
html{
    scroll-behavior: smooth;
}
::-webkit-scrollbar{
    display: none;
}
body{
    font-family: 'Poppins', sans-serif;
    background: rgb(20, 20, 20);
    background: var(--color-bg);
    color:#fff;
    color:var(--color-white);
    line-height: 1.7;
    background-image: url(data:image/png;base64,);
}

/* --------------------------------------------- */

.container{
    width: 75%;
    width: var(--container-width-lg);
    margin: 0 auto;
}
h1,h2,h3,h4,h5{
    font-weight: 500;
}
h1{
    font-size: 2.5rem;
}
section{
    margin-top: 8rem;
}
section > h2,section>h5{
    text-align: center;
    color: rgba(255, 255, 255, 0.6);
    color: var(--color-light);
}
section > h2{
    color: #ffbf00;
    color: var(--color-primary);
    margin-bottom: 3rem;
}
.text-light{
    color: rgba(255, 255, 255, 0.6);
    color: var(--color-light);
}
a{
    color: #ffbf00;
    color: var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}
a:hover{
    color: #fff;
    color: var(--color-white);
}
.btn{
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    color: #ffbf00;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid #ffbf00;
    border: 1px solid var(--color-primary);
    -webkit-transform: all 400ms ease;
            transform: all 400ms ease;
    -webkit-transform: var(--transition);
            transform: var(--transition);
}
.btn:hover{
    background: #fff;
    background: var(--color-white);
    color: rgb(20, 20, 20);
    color: var(--color-bg);
    border-color: transparent;
}
.btn-primary{
    background: #ffbf00;
    background: var(--color-primary);
    color: rgb(20, 20, 20);
    color: var(--color-bg);
}
img{
    display: block;
    width: 100%;
    object-fit: cover;
}
/* --------------------medium----------------------- */
@media screen and (max-width:1024px){
    .container{
        width: 86%;
        width: var(--container-width-md);
    }
    section{
        margin-top: 6rem;
    }
}
/* --------------------small----------------------- */
@media screen and (max-width:600px){
    .container{
        width: 90%;
        width: var(--container-width-sm);
    }
    section > h2{
        margin-top: 2rem;
    }
}
